<template>
  <div>
    <!---->
    <Breadcrumb class="breadcrumb">
      <BreadcrumbItem>
        <Icon type="ios-home-outline"></Icon>基础设置
      </BreadcrumbItem>
      <BreadcrumbItem class="curBreadcrumb">
        <Icon type="logo-buffer"></Icon>人员设置
      </BreadcrumbItem>
      <span class="screenContent">
        <img
          style="width: 11px;margin-right: 8px;margin-left: 10px;"
          src="../assets/day.png"
        >
        {{ curTime }}</span>
      <span class="screenContent">
        <img
          style="width: 10.5px;margin-right: 8px"
          src="../assets/screen.png"
        >
        <a
          href="javascript:void(0)"
          @click="isShowScreen"
        >
          筛选
          <Icon type="ios-arrow-down"></Icon>
        </a>
      </span>
    </Breadcrumb>
    <Content
      v-if="isScreen"
      class="content isScreen"
      :style="{padding: '24px', background: '#fff', marginBottom: '10px'}"
    >
      <Form
        :model="searchData"
        :label-width="62"
      >
        <Row>
          <Col
            :xs="{ span: 5 }"
            :lg="{ span: 6}"
          >
          <FormItem label="所属机构">
            <Select
              v-model="searchData.orgId"
              clearable
              :transfer="transferValue"
              filterable
              placeholder="请选择所属机构"
            >
              <Option
                v-for="item in orgList"
                :key="item.orgId"
                :value="item.orgId"
              >{{ item.orgName }}</Option>
            </Select>
          </FormItem>
          </Col>
          <Col
            :xs="{ span: 11, offset: 1 }"
            :lg="{ span: 6}"
          >
          <FormItem label="姓名">
            <Input
              v-model="searchData.linkManName"
              placeholder="请填写姓名"
            ></Input>
          </FormItem>
          </Col>
          <Col
            style="text-align: right"
            :xs="{ span: 1}"
            :lg="{ span: 2, offset: 1 }"
          >
          <Button
            type="primary"
            @click="getContactData"
          >
            确认
          </Button>
          </Col>
        </Row>
      </Form>
    </Content>
    <Content
      class="content"
      :style="{padding: '14px 24px', minHeight: '220px', background: '#fff'}"
    >
      <Button
        type="primary"
        icon="md-add"
        @click="createData"
      >
        新增
      </Button>
      <Button
        type="primary"
        icon="ios-create"
        @click="modifyData"
      >
        修改
      </Button>
      <Button
        type="primary"
        icon="md-close"
        @click="deleteData"
      >
        删除
      </Button>
      <Button icon="md-print" @click.stop="printContent(4)">
        打印
      </Button>
      <Button icon="md-exit" @click="excelExcel('/linkMan/exportLinkmanListExcel')">
        导出
      </Button>
      <Table
        ref="checkTable"
        highlight-row
        class="table"
        :columns="columns"
        :data="data"
        @on-current-change="selectRow"
      ></Table>
      <!-- 分页 -->
      <Page
        style="text-align: right"
        :total="total"
        :current="searchData.pageNo"
        :page-size="searchData.pageSize"
        show-sizer
        transfer
        @on-change="pageNoChange"
        @on-page-size-change="pageSizeChange"
      />
      <!--详情弹出框-->
      <Modal
        v-model="selTypeMod"
        class="selStyle specialWidth"
        :mask-closable="false"
      >
        <p slot="header">
          <span v-if="!isDisabled && !showTitle">请设置联系人信息</span>
          <span v-else>联系人信息</span>
        </p>
        <Tabs
          :animated="false"
          class="innerTabStyle"
          value="name1"
        >
          <TabPane
            label="信息设置"
            name="name1"
          >
            <Form
              ref="form"
              :model="form"
              :rules="ruleValidateType"
              :label-width="80"
            >
              <Row>
                <Col span="12">
                <FormItem
                  label="姓名"
                  prop="linkManName"
                >
                  <Input
                    v-model.trim="form.linkManName"
                    :disabled="isDisabled"
                    placeholder="请填写姓名"
                  ></Input>
                </FormItem>
                </Col>
                <Col span="12">
                <FormItem
                  label="所属机构"
                  prop="orgId"
                >
                  <Select
                    v-if="!isDisabled"
                    v-model="form.orgId"
                    clearable
                    :transfer="transferValue"
                    filterable
                    placeholder="请选择所属机构"
                    @on-change="getBusinessData"
                  >
                    <Option
                      v-for="item in orgList"
                      :key="item.orgId"
                      :value="item.orgId"
                    >{{ item.orgName }}</Option>
                  </Select>
                  <Input
                    v-else
                    v-model.trim="form.orgName"
                    disabled
                    placeholder="请选择所属机构"
                  ></Input>
                </FormItem>
                </Col>
              </Row>
              <Row>
                <Col span="12">
                <FormItem
                  label="所属部门"
                  prop="officeId"
                >
                  <Select
                    v-if="!isDisabled"
                    v-model="form.officeId"
                    clearable
                    :transfer="transferValue"
                    filterable
                    placeholder="请选择所属部门"
                  >
                    <Option
                      v-for="item in officeList"
                      :key="item.officeId"
                      :value="item.officeId"
                    >{{ item.officeName }}</Option>
                  </Select>
                  <Input
                    v-else
                    v-model.trim="form.officeName"
                    disabled
                    placeholder="请填写部门名称"
                  ></Input>
                </FormItem>
                </Col>
                <Col span="12">
                <FormItem
                  label="联系电话"
                  prop="linkManPhone"
                >
                  <Input
                    v-model.trim="form.linkManPhone"
                    :disabled="isDisabled"
                    placeholder="请填写电话"
                  ></Input>
                </FormItem>
                </Col>
              </Row>
              <Row>
                <Col span="12">
                <FormItem
                  label="电子邮箱"
                  prop="linkManEmail"
                >
                  <Input
                    v-model.trim="form.linkManEmail"
                    :disabled="isDisabled"
                    placeholder="请填写电子邮箱"
                  ></Input>
                </FormItem>
                </Col>
                <Col span="12">
                <FormItem label="备注">
                  <Input
                    v-model.trim="form.linkManRemark"
                    :disabled="isDisabled"
                    type="textarea"
                    :autosize="{minRows: 1,maxRows: 5}"
                    placeholder="请填写备注"
                  ></Input>
                </FormItem>
                </Col>
              </Row>
            </Form>
          </TabPane>
          <TabPane
            class="uploadStyle"
            label="附件"
            name="name2"
          >
            <uploadFile ref="uploadFile" :is-disabled="isDisabled" :form="form" />
          </TabPane>
        </Tabs>
        <p
          slot="footer"
          style="text-align: left"
        >
          <Button icon="md-print">
            打印
          </Button>
          <Button icon="md-exit">
            导出
          </Button>
          <Button
            v-if="!isDisabled"
            style="float: right;margin-left: 8px"
            @click="resetType('form')"
          >
            取消
          </Button>
          <Button
            v-if="!isDisabled"
            style="float: right"
            type="primary"
            @click="submitType('form')"
          >
            确认
          </Button>
        </p>
      </Modal>
      <!--/详情弹出框-->
    </Content>
  </div>
</template>

<script>
import uploadFile from '@/components/fileUpload.vue'
import excelExcel from '@/mixins/exportExcel.js'
export default {
  name: 'ContactManage',
  components: {
    uploadFile
  },
  mixins: [excelExcel],
  data() {
    // 自定义序号
    const indexMethod = (row) => {
      const index = row._index
      return this.searchData.pageSize * (this.searchData.pageNo - 1) + index + 1;
    }
    return {
      // 分页的总数
      total: 0,
      // 部门下拉框
      officeList: [],
      currentRow: {},
      // 部门下拉框
      orgList: [],
      //  是否显示 修改 弹出框名称
      showTitle: false,
      isSelected: false,
      isDisabled: false,
      transferValue: true,
      form: {
        officeId: null,
        officeName: '',
        linkManPhone: null,
        linkManEmail: '',
        linkManName: '',
        orgId: null,
        orgName: '',
        linkManRemark: ''
      },
      ruleValidateType: {
        linkManEmail: [
          { required: false, message: '电子邮箱不能为空', trigger: 'change' },
          { type: 'email', message: '请正确填写邮箱', trigger: 'change' }
        ],
        linkManPhone: [
          { required: true, message: '电话号码不能为空', trigger: 'change' }
        ],
        linkManName: [
          { required: true, message: '请填写姓名', trigger: 'change' }
        ],
        orgId: [
          { type: 'number', required: true, message: '请填写机构名称', trigger: 'blur' }
        ]
      },
      // 筛选内容
      searchData: {
        orgId: '',
        linkManName: '',
        ascending: false,
        orderBy: 'linkManId',
        pageNo: 1,
        pageSize: 10
      },
      isScreen: false,
      curTime: '',
      showContact: false,
      selTypeMod: false,
      columns: [
        {
          type: 'index',
          title: '序号',
          width: 60,
          align: 'center',
          indexMethod: indexMethod
        },
        {
          title: '姓名',
          width: 80,
          align: 'left',
          render: (h, params) => {
            return h('span', {
              style: {
                color: '#2d8cf0',
                cursor: 'pointer'
              },
              on: {
                click: () => {
                  this.showInfo(params.row)
                }
              }
            }, params.row.linkManName);
          }
        },
        {
          title: '所属机构',
          key: 'orgName'
        },
        {
          title: '所属部门',
          key: 'officeName'
        },
        {
          title: '联系电话',
          key: 'linkManPhone'
        },
        {
          title: '电子邮箱',
          key: 'linkManEmail'
        }
      ],
      data: [
      ]
    }
  },
  mounted() {
    // 获取机构名称
    this.getAllList()
    var myDate = new Date();
    const time = myDate.toLocaleString('chinese', { hour12: false });
    this.curTime = time.substring(0, 15)
  },
  methods: {
    getAllList() {
      this.getBusinessData()
      this.getOrganize()
      this.getContactData()
    },
    pageNoChange(pageNo) {
      this.searchData.pageNo = pageNo
      this.getAllList()
    },
    pageSizeChange(pageSize) {
      this.searchData.pageSize = pageSize
      this.getAllList()
    },
    deleteData() {
      const that = this;
      if (!that.isSelected) {
        that.$Message.warning('您未选择需要删除的内容');
        return
      }
      that.$Modal.confirm({
        title: '删除',
        content: '确认删除此数据吗？',
        onOk: () => {
          that.$Spin.show({
            render: (h) => {
              return h('div', [
                h('Icon', {
                  'class': 'demo-spin-icon-load',
                  props: {
                    type: 'ios-loading',
                    size: 18
                  }
                }),
                h('div', '加载中')
              ])
            }
          });
          that.commonUtil.ajaxDeleteUtil('/linkMan/deleteLinkMan/' + that.currentRow.linkManId, (res) => {
            // 关闭数据加载
            that.$Spin.hide();
            if (res.code === 1) {
              that.isSelected = false;
              that.$Message.success(res.msg);
              that.getContactData()
            } else {
              that.$Message.error(res.msg);
            }
          }, (error) => {
            that.$Message.error('网络连接错误');
            that.$Spin.hide();
          }, that)
        }
      });
    },
    // 获取机构名称
    getOrganize() {
      const that = this;
      that.$Spin.show({
        render: (h) => {
          return h('div', [
            h('Icon', {
              'class': 'demo-spin-icon-load',
              props: {
                type: 'ios-loading',
                size: 18
              }
            }),
            h('div', '加载中')
          ])
        }
      });
      that.commonUtil.ajaxPostUtil('/organization/searchOrganization/list', { orgType: '', orgName: '', pageNo: 0, pageSize: 0 }, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          that.orgList = res.data.orgList
        } else {
          that.$Message.error(res.msg);
        }
      }, (error) => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
    },
    getContactData() {
      const that = this;
      that.isSelected = false;
      that.$set(that, 'data', []) // 需要做这个操作，以防空数据留至其他行
      that.$Spin.show({
        render: (h) => {
          return h('div', [
            h('Icon', {
              'class': 'demo-spin-icon-load',
              props: {
                type: 'ios-loading',
                size: 18
              }
            }),
            h('div', '加载中')
          ])
        }
      });
      that.commonUtil.ajaxPostUtil('/linkMan/searchLinkMan/list', that.searchData, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          const listData = res.data
          that.total = listData.totalCount
          that.data = listData.linkManList
        } else {
          that.$Message.error(res.msg);
        }
      }, (error) => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
      that.commonUtil.getAuditCountApi(that)
    },
    clearFormData() {
      this.form = {
        officeId: null,
        officeName: '',
        linkManPhone: null,
        linkManEmail: '',
        linkManName: '',
        orgId: null,
        orgName: '',
        linkManRemark: ''
      }
      this.$refs['form'].resetFields();
      const uploadFile = this.$refs.uploadFile
      if (uploadFile) {
        uploadFile.clearFileItems()
      }
    },
    // 打开详情弹出来
    showInfo(row) {
      const data = Object.assign({}, row)
      this.clearFormData()
      this.isDisabled = true;
      this.form = data
      this.$nextTick(() => {
        this.$refs.uploadFile.getFileItems()
      })
      this.selTypeMod = true
    },
    // 选中一行进行修改数据
    selectRow(currentRow) {
      this.currentRow = currentRow;
      this.isSelected = true;
    },
    // 修改数据
    modifyData() {
      const that = this
      if (!that.isSelected) {
        that.$Message.warning('您未选择需要修改的内容');
        return
      }
      that.clearFormData();
      const row = Object.assign({}, that.currentRow);
      // 显示弹出框名称
      that.showTitle = true;
      that.isDisabled = false;
      that.showData(row)
    },
    showData(data) {
      this.getBusinessData(data.orgId, (flag) => {
        if (flag) {
          this.form = data
        }
        this.$nextTick(() => {
          this.$refs.uploadFile.getFileItems()
        })
        this.selTypeMod = true
      })
    },
    createData() {
      this.clearFormData();
      // 是否显示 修改 弹出框名称
      this.showTitle = false;
      this.isDisabled = false;
      this.selTypeMod = true;
    },
    submitType(name) {
      const that = this;
      const form = that.form
      form.fileIds = that.$refs.uploadFile.getFileIds()
      that.$refs[name].validate((valid) => {
        if (valid) {
          that.$Spin.show({
            render: (h) => {
              return h('div', [
                h('Icon', {
                  'class': 'demo-spin-icon-load',
                  props: {
                    type: 'ios-loading',
                    size: 18
                  }
                }),
                h('div', '加载中')
              ])
            }
          });
          // 修改
          if (that.showTitle) {
            that.commonUtil.ajaxPutUtil('/linkMan/updateLinkMan', form, (res) => {
              // 关闭数据加载
              that.$Spin.hide();
              if (res.code === 1) {
                that.selTypeMod = false;
                that.$Message.success(res.msg);
                that.getContactData()
              } else {
                that.$Message.error(res.msg);
              }
            }, (error) => {
              that.$Message.error('网络连接错误');
              // 关闭数据加载
              that.$Spin.hide();
            }, that)
          } else {
            that.commonUtil.ajaxPostUtil('/linkMan/addLinkMan', form, (res) => {
              // 关闭数据加载
              that.$Spin.hide();
              if (res.code === 1) {
                that.selTypeMod = false;
                that.$Message.success(res.msg);
                that.getContactData()
              } else {
                that.$Message.error(res.msg);
              }
            }, (error) => {
              that.$Message.error('网络连接错误');
              // 关闭数据加载
              that.$Spin.hide();
            }, that)
          }
          that.currentRow = {}
        } else {
          that.$Message.error('请填写好信息');
        }
      })
    },
    resetType(name) {
      this.$refs[name].resetFields();
      this.selTypeMod = false;
    },
    // 筛选按钮
    isShowScreen() {
      this.isScreen = !this.isScreen;
    },
    // 弹框表格结束
    ok() {

    },
    // 部门
    getBusinessData(id, callback) {
      this.$Spin.show({
        render: (h) => {
          return h('div', [
            h('Icon', {
              'class': 'demo-spin-icon-load',
              props: {
                type: 'ios-loading',
                size: 18
              }
            }),
            h('div', '加载中')
          ])
        }
      });
      const orgId = id || null
      if (orgId && !callback) {
        this.form.officeId = null
      }
      this.commonUtil.ajaxPostUtil('/office/searchOffice/list', { orgId: orgId, officeName: '', pageNo: 0, pageSize: 0 }, (res) => {
        // 关闭数据加载
        this.$Spin.hide();
        if (res.code === 1) {
          this.officeList = res.data.officeList
          if (callback) callback(true)
        } else {
          this.$Message.error(res.data);
          if (callback) callback(false)
        }
      }, (error) => {
        if (callback) callback(false)
        this.$Message.error('网络连接错误');
        // 关闭数据加载
        this.$Spin.hide();
      }, this)
    }
  }
}
</script>

<style scoped>
.ivu-drawer-body > div {
  margin: 20px 0;
}
.ivu-drawer-body > div b {
  margin-left: 3px;
}
/*<!--选择弹出框的样式-->*/
.selStyle .ivu-radio-wrapper {
  font-weight: bold;
}
.selStyle .ivu-modal-body {
  line-height: 2rem;
}
.selStyle .ivu-radio-wrapper {
  margin-right: 21px;
}
.ivu-page-options-sizer {
  margin-right: 0;
}
.table {
  margin: 15px 0;
}
.breadcrumb i {
  margin-right: 8px;
}
.ivu-breadcrumb {
  font-size: 12px;
  margin: 20px 0;
}
.ivu-menu-submenu-title {
  font-weight: bold;
}
.ivu-menu-submenu-title span > i,
.ivu-menu-submenu-title > i {
  margin-right: 4px;
}
.ivu-menu-vertical .ivu-menu-item,
.ivu-menu-vertical .ivu-menu-submenu-title {
  font-size: 12px;
  color: #495060;
}
.ivu-menu-light.ivu-menu-vertical .ivu-menu-item-active:not(.ivu-menu-submenu) {
  font-weight: bold;
}
.ivu-menu-submenu.ivu-menu-opened {
  padding-top: 8px;
}
</style>




